[...document.querySelectorAll('[data-typewriter]')].forEach((e) => {
  try {
    let words = JSON.parse(e.getAttribute('data-words').split("'").join('"'))
    let banner = [...e.querySelectorAll('[data-slot]')]
    let type = (str, elem) => {
      if (str.length !== 0) {
        let update = () => elem.innerHTML = elem.innerHTML + str[0]
        requestAnimationFrame ? requestAnimationFrame(update) : update()
        setTimeout(() => type(str.slice(1), elem), str[0] === ' ' ? 0 : 80);
      }
    }
    let update = (idx) => {
      banner.forEach((b, bid) => {
        b.innerHTML = ''
        type(words[idx][bid], b)
      })
      setTimeout(() => update((idx + 1 ) % words.length), 3000)
    }
    update(0)
  } catch (e) {
  }
})