let yCord = () => window.pageYOffset
let height = Math.min(document.documentElement.clientHeight, 700)
let heights = [...document.querySelectorAll('.container')].map(e => e.getBoundingClientRect().height)
let pageN = () => heights.reduce(([prevN, y], h) => {
  return y - h > 0 ? [prevN + 1, y - h] : [prevN + (y / h), 0]
}, [0, yCord()])[0]

let $id = (id) => document.getElementById(id)
let $class = (el, cls, setTo) => {
  let clsList = el.classList
  if (clsList) {
    if (setTo) {
      clsList.add(cls)
    } else {
      clsList.remove(cls)
    }
  } else {
    if (setTo) {
      el.className += ' ' + cls;
    } else {
      el.className = el.className.replace(new RegExp('(^|\\b)' + cls.split(' ').join('|') + '(\\b|$)', 'gi'), ' ');
    }
  }
}


let navs = [...document.querySelectorAll('.nav')]
let activeNavIndex = 0
let scrollWaitTimer = null

let finishScroll = () => {
  let n = pageN(), floor = Math.floor(n), ceiling = floor + 1
  if (n < .2)
    activeNavIndex = 0
  if (floor === 0 && n !== 0)
    activeNavIndex = null
  if (floor >= 1)
    activeNavIndex = floor
  if (ceiling - n < 0.3 && ceiling >= 1)
    activeNavIndex = ceiling
  // update
  navs.forEach((e, idx) => {
    $class(e, 'active', activeNavIndex == idx)
  })
}

document.addEventListener('scroll', () => {
  finishScroll()
})

finishScroll()