import { $class } from './util.js'

[...document.querySelectorAll('[data-video-control]')].forEach((e) => {
  let vid = document.querySelector(e.getAttribute('data-video-control'))
  if (!vid) return
  let onClick = () => {
    if (vid.paused || vid.ended) {
      vid.play()
      $class(e, 'paused', false)
      let reqFullScreen = vid.requestFullscreen || vid.webkitRequestFullscreen
      if (reqFullScreen) {
        reqFullScreen()
      }
    } else {
      vid.pause()
      $class(e, 'paused', true)
    }
  }
  e.addEventListener('click', onClick)
  e.addEventListener('ontouchstart', onClick)
  vid.addEventListener('click', onClick)
  vid.addEventListener('ontouchstart', onClick)
  vid.addEventListener('pause', () => {
    $class(e, 'paused', true)
  })
})