export let $id = (id) => document.getElementById(id)
export let $class = (el, cls, setTo) => {
  let clsList = el.classList
  if (clsList) {
    if (setTo) {
      clsList.add(cls)
    } else {
      clsList.remove(cls)
    }
  } else {
    if (setTo) {
      el.className += ' ' + cls;
    } else {
      el.className = el.className.replace(new RegExp('(^|\\b)' + cls.split(' ').join('|') + '(\\b|$)', 'gi'), ' ');
    }
  }
}